import { useState, useEffect } from "react";
import { Card } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Tabs, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Avatar } from "@/components/ui/avatar";
import { Progress } from "@/components/ui/progress";
import {
  ChevronLeft,
  ChevronRight,
  TrendingUp,
  MessageSquare,
  Flame,
  Brain,
  BarChart,
  User,
  ThumbsUp,
  FileText,
  Search,
} from "lucide-react";

const ITEMS_PER_PAGE = 5;

// Score indicator component for displaying numeric scores with a progress bar
const ScoreIndicator = ({ score, maxScore = 5 }) => {
  const percentage = (score / maxScore) * 100;
  let color = "bg-red-500";

  if (percentage >= 80) color = "bg-green-500";
  else if (percentage >= 60) color = "bg-yellow-500";

  return (
    <div className="w-full space-y-2">
      <div className="flex justify-between text-sm">
        <span className="text-neutral-600">
          {score}/{maxScore}
        </span>
        <span className="text-neutral-600">{percentage}%</span>
      </div>
      <Progress value={percentage} className={`h-2 ${color}`} />
    </div>
  );
};

// Card component for displaying individual candidate information
const CandidateCard = ({ candidate, onSelect }) => (
  <Card className="p-4 hover:shadow-md transition-shadow">
    <div className="flex items-start justify-between">
      <div className="space-y-1">
        <div className="flex items-center space-x-2">
          <Avatar className="h-10 w-10">
            <User className="h-6 w-6" />
          </Avatar>
          <div>
            <h3 className="font-medium">{candidate.Name}</h3>
            <p className="text-sm text-neutral-500">{candidate.Department}</p>
          </div>
        </div>
      </div>
      <Button onClick={() => onSelect(candidate)} variant="outline">
        View Details
      </Button>
    </div>
    <div className="mt-4 space-y-3">
      <ScoreIndicator score={candidate.overallscore} />
      <div className="flex gap-2">
        <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-green-100 text-green-800">
          Communication: {candidate.communicationScore}
        </span>
        <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-yellow-100 text-yellow-800">
          Innovation: {candidate.innovationScore}
        </span>
      </div>
    </div>
  </Card>
);

// Section component for displaying detailed information with an icon
const DetailSection = ({ title, icon: Icon, children }) => (
  <Card className="p-6 space-y-4">
    <div className="flex items-center space-x-2">
      <div className="p-2 rounded-full bg-primary-50">
        <Icon className="h-5 w-5 text-primary-500" />
      </div>
      <h2 className="text-lg font-semibold">{title}</h2>
    </div>
    <div className="space-y-3">{children}</div>
  </Card>
);

export default function AdminDashboard() {
  // State management
  const [candidates, setCandidates] = useState([]);
  const [selectedCandidate, setSelectedCandidate] = useState(null);
  const [error, setError] = useState(null);
  const [currentPage, setCurrentPage] = useState(1);
  const [searchTerm, setSearchTerm] = useState("");
  const [loading, setLoading] = useState(true);
  const [activeTab, setActiveTab] = useState("candidates");

  // Fetch candidates data on component mount
  useEffect(() => {
    const fetchCandidates = async () => {
      try {
        setLoading(true);
        const response = await fetch("/api/airTable");
        if (!response.ok) throw new Error("Failed to fetch candidates");
        const data = await response.json();
        setCandidates(data.data);
      } catch (err) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    };
    fetchCandidates();
  }, []);

  // Filter candidates based on search term
  const filteredCandidates = candidates.filter((candidate) =>
    candidate.Name.toLowerCase().includes(searchTerm.toLowerCase())
  );

  // Calculate pagination
  const totalPages = Math.ceil(filteredCandidates.length / ITEMS_PER_PAGE);
  const paginatedCandidates = filteredCandidates.slice(
    (currentPage - 1) * ITEMS_PER_PAGE,
    currentPage * ITEMS_PER_PAGE
  );

  // Handle candidate selection and parse JSON data
  const handleSelectCandidate = (candidate) => {
    try {
      setSelectedCandidate({
        ...candidate,
        detailedAnalysis: JSON.parse(candidate.detailedAnalysis || "{}"),
        behavioralAnalysis: JSON.parse(candidate.behavioralAnalysis || "{}"),
      });
    } catch (err) {
      setError("Error parsing candidate data");
      console.error("Error parsing candidate data:", err);
    }
  };

  // Loading state
  if (loading) {
    return (
      <div className="flex h-screen items-center justify-center">
        <div className="space-y-4 text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-primary-500 mx-auto" />
          <p className="text-neutral-600">Loading dashboard...</p>
        </div>
      </div>
    );
  }

  // Error state
  if (error) {
    return (
      <Alert variant="destructive" className="m-4">
        <AlertDescription>{error}</AlertDescription>
      </Alert>
    );
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="space-y-6">
        <div className="flex justify-between items-center">
          <h1 className="text-2xl font-bold">Admin Dashboard</h1>
          {selectedCandidate && (
            <Button
              variant="outline"
              onClick={() => setSelectedCandidate(null)}
              className="flex items-center space-x-2"
            >
              <ChevronLeft className="h-4 w-4" />
              <span>Back to List</span>
            </Button>
          )}
        </div>

        {!selectedCandidate ? (
          <>
            <Tabs value={activeTab} onValueChange={setActiveTab}>
              <TabsList>
                <TabsTrigger value="candidates">Candidates</TabsTrigger>
                <TabsTrigger value="leaderboard">Leaderboard</TabsTrigger>
              </TabsList>
            </Tabs>

            <div className="relative">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-neutral-400 h-4 w-4" />
              <Input
                className="pl-10"
                placeholder="Search candidates..."
                value={searchTerm}
                onChange={(e) => {
                  setSearchTerm(e.target.value);
                  setCurrentPage(1);
                }}
              />
            </div>

            <div className="space-y-4">
              {activeTab === "candidates" ? (
                paginatedCandidates.map((candidate, index) => (
                  <CandidateCard
                    key={`candidate-${index}`}
                    candidate={candidate}
                    onSelect={handleSelectCandidate}
                  />
                ))
              ) : (
                <div className="space-y-4">
                  {[...candidates]
                    .sort((a, b) => b.overallscore - a.overallscore)
                    .map((candidate, index) => (
                      <CandidateCard
                        key={`leaderboard-${index}`}
                        candidate={{
                          ...candidate,
                          rank: index + 1,
                        }}
                        onSelect={handleSelectCandidate}
                      />
                    ))}
                </div>
              )}
            </div>

            {totalPages > 1 && (
              <div className="flex justify-between items-center mt-6">
                <span className="text-sm text-neutral-600">
                  Page {currentPage} of {totalPages}
                </span>
                <div className="flex space-x-2">
                  <Button
                    variant="outline"
                    onClick={() => setCurrentPage((p) => Math.max(1, p - 1))}
                    disabled={currentPage === 1}
                  >
                    <ChevronLeft className="h-4 w-4" />
                  </Button>
                  <Button
                    variant="outline"
                    onClick={() =>
                      setCurrentPage((p) => Math.min(totalPages, p + 1))
                    }
                    disabled={currentPage === totalPages}
                  >
                    <ChevronRight className="h-4 w-4" />
                  </Button>
                </div>
              </div>
            )}
          </>
        ) : (
          <div className="space-y-6">
            <DetailSection title="Personal Information" icon={User}>
              <div className="grid md:grid-cols-2 gap-4">
                <div>
                  <p className="text-sm text-neutral-500">Name</p>
                  <p className="font-medium">{selectedCandidate.Name}</p>
                </div>
                <div>
                  <p className="text-sm text-neutral-500">Department</p>
                  <p className="font-medium">{selectedCandidate.Department}</p>
                </div>
                <div>
                  <p className="text-sm text-neutral-500">College</p>
                  <p className="font-medium">{selectedCandidate.College}</p>
                </div>
                <div>
                  <p className="text-sm text-neutral-500">Phone</p>
                  <p className="font-medium">
                    {selectedCandidate.phone_number}
                  </p>
                </div>
              </div>
            </DetailSection>

            <DetailSection title="Performance Scores" icon={TrendingUp}>
              <div className="space-y-4">
                <div>
                  <p className="text-sm font-medium mb-2">Communication</p>
                  <ScoreIndicator
                    score={selectedCandidate.communicationScore}
                  />
                </div>
                <div>
                  <p className="text-sm font-medium mb-2">Innovation</p>
                  <ScoreIndicator score={selectedCandidate.innovationScore} />
                </div>
                <div>
                  <p className="text-sm font-medium mb-2">Fire in Belly</p>
                  <ScoreIndicator score={selectedCandidate.fireInBellyScore} />
                </div>
                <div className="pt-4 border-t">
                  <p className="text-lg font-semibold">
                    Overall Score: {selectedCandidate.overallscore}
                  </p>
                </div>
              </div>
            </DetailSection>

            <div className="grid md:grid-cols-2 gap-6">
              <DetailSection title="Detailed Analysis" icon={BarChart}>
                {Object.entries(selectedCandidate.detailedAnalysis).map(
                  ([key, value]) => (
                    <div key={`detail-${key}`} className="space-y-2">
                      <h3 className="text-sm font-medium capitalize">{key}</h3>
                      <p className="text-sm text-neutral-600">{value}</p>
                    </div>
                  )
                )}
              </DetailSection>

              <DetailSection title="Behavioral Analysis" icon={Brain}>
                {Object.entries(selectedCandidate.behavioralAnalysis).map(
                  ([key, value]) => (
                    <div key={`behavior-${key}`} className="space-y-2">
                      <h3 className="text-sm font-medium capitalize">{key}</h3>
                      <p className="text-sm text-neutral-600">{value}</p>
                    </div>
                  )
                )}
              </DetailSection>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}
